import { writable } from "svelte/store";
const componentMapStore = writable({});


let  componentMap= {};

function initComponentMapStore() {
	 console.log("initMapStore called");

	 componentMapStore.set(componentMap);	 
}

function addKVIdRef(comp_id, comp_ref) {
    componentMap[comp_id] = comp_ref;
    console.log('Updated componentMap:', comp_id, " ", comp_ref);
    componentMapStore.set(componentMap); // Notify subscribers of the change
}

function updateComponentTwClass(comp_id, twClassStr){
	 console.log("updating comp_id = ", comp_id, " ", componentMap[comp_id]);
	 componentMap[comp_id].updateTwClass(twClassStr)

}

export { initComponentMapStore, addKVIdRef, updateComponentTwClass};